import React, { Fragment, useContext, useState } from 'react';
import { useForm, Controller } from 'react-hook-form';
import { Row, Col, Input, Select, Button, notification } from 'antd';
import FormControl from 'components/UI/FormControl/FormControl';
import DatePicker from 'components/UI/AntdDatePicker/AntdDatePicker';
import { FormTitle } from './AccountSettings.style';
import { AuthContext } from 'context/AuthProvider';
import AxiosFetch from 'library/hooks/AxiosFetch';

const AgentCreateOrUpdateForm = () => {

  const { user } = useContext(AuthContext);

  const getFirstName  = () => {
    const nameArray = user.name.split(" ");
    return nameArray[0];
  }

  const getlastName  = () => {
    const nameArray = user.name.split(" ");
    return nameArray[1];
  }

  const {
    control,
    formState: { errors },
    handleSubmit,
  } = useForm();
  
  const openNotificationWithIcon = (type, title, description) => {
    notification[type]({
      message: title,
      description: description,
      placement: 'bottomRight'
    });
  };

  const [isCurrentPasswordCorrect, setIsCurrentPasswordCorrect] = useState(true);
  const onSubmit = async (data) => {    
    if(data.current_password)
    {
      if(data.current_password !== user.password)
      {
        openNotificationWithIcon('error', 'Error!', "Current Password is not correct.")
        return;
      }
      if(data.new_password == '' || data.confirm_new_password == '')
      {
        openNotificationWithIcon('error', 'Error!', "Input correct new password!")
        return;
      }
    }
    if(data.new_password || data.confirm_new_password)
    {
      if(data.new_password !== data.confirm_new_password || data.confirm_new_password == '')
      {
        openNotificationWithIcon('error', 'Error!', "Confirm Password is not correct.")
        return;
      }
    }

    let url = '/updateProfile';
    const params = data;
    params.user_id = user.id;

    let res = await AxiosFetch(url, params, "POST");
    console.log(params)
    if(!res.data.success)
    {
      openNotificationWithIcon('error', 'Error!', res.data.message)
      return;
    }
    openNotificationWithIcon('success', 'Success!', "Your profile has been updated successfully.")
  }

  return (
    <Fragment>
      <FormTitle>Profile Information</FormTitle>
      <form className="form-container" onSubmit={handleSubmit(onSubmit)}>
        <Row gutter={30}>
          <Col lg={12}>
            <FormControl
              label="First name"
              htmlFor="firstName"
              error={user.name === null && <span>This field is required!</span>}
            >
              <Controller
                name="firstName"
                defaultValue={getFirstName()}
                control={control}
                rules={{ required: true }}
                render={({ field: { onChange, onBlur, value } }) => (
                  <Input onChange={onChange} onBlur={onBlur} value={ value } />
                )}
              />
            </FormControl>
          </Col>
          <Col lg={12}>
            <FormControl
              label="Last name"
              htmlFor="lastName"
              error={user.name === null && <span>This field is required!</span>}
            >
              <Controller
                name="lastName"
                defaultValue={getlastName()}
                control={control}
                rules={{ required: true }}
                render={({ field: { onChange, onBlur, value } }) => (
                  <Input onChange={onChange} onBlur={onBlur} value={ value } />
                )}
              />
            </FormControl>
          </Col>
        </Row>
          
        <Row gutter={30}>
          <Col md={12}>
            <FormControl
                label="Email address"
                htmlFor="email"
                error={
                  user.email === null && (
                    <>
                      {errors.email?.type === 'required' && (
                        <span>This field is required!</span>
                      )}
                      {errors.email?.type === 'pattern' && (
                        <span>Please enter a valid email address!</span>
                      )}
                    </>
                  )
                }
              >
                <Controller
                  name="email"
                  defaultValue={user.email}
                  control={control}
                  rules={{
                    required: true,
                    pattern: /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/,
                  }}
                  render={({ field: { onChange, onBlur, value } }) => (
                    <Input
                      type="email"
                      onChange={onChange}
                      onBlur={onBlur}
                      value={value}
                    />
                  )}
                />
              </FormControl>
          </Col>       
          <Col md={12}>
            <FormControl
              label="Current Password"
              htmlFor="current_password"
            >
              <Controller
                name="current_password"
                defaultValue=""
                control={control}
                render={({ field: { onChange, onBlur, value } }) => (
                  <Input.Password onChange={onChange} onBlur={onBlur} value={value} />
                )}
              />
            </FormControl>
          </Col>
        </Row>
        <Row gutter={30}>
          <Col md={12}>
            <FormControl
              label="New Password"
              htmlFor="new_password"
              error={
                errors.new_password && (
                  <>                    
                    {errors.new_password?.type === 'minLength' && (
                      <span>Password must be at lest 6 characters!</span>
                    )}
                    {errors.new_password?.type === 'maxLength' && (
                      <span>Password must not be longer than 20 characters!</span>
                    )}
                  </>
                )
              }
            >
              <Controller
                name="new_password"
                defaultValue=""
                control={control}
                rules={{ minLength: 6, maxLength: 20 }}
                render={({ field: { onChange, onBlur, value } }) => (
                  <Input.Password onChange={onChange} onBlur={onBlur} value={value} />
                )}
              />
            </FormControl>
          </Col>
          <Col md={12}>
            <FormControl
              label="Confirm New Password"
              htmlFor="confirm_new_password"
              error={
                errors.confirm_new_password && (
                  <>                    
                    {errors.confirm_new_password?.type === 'minLength' && (
                      <span>Password must be at lest 6 characters!</span>
                    )}
                    {errors.confirm_new_password?.type === 'maxLength' && (
                      <span>Password must not be longer than 20 characters!</span>
                    )}
                  </>
                )
              }
            >
              <Controller
                name="confirm_new_password"
                defaultValue=""
                control={control}
                rules={{ minLength: 6, maxLength: 20 }}
                render={({ field: { onChange, onBlur, value } }) => (
                  <Input.Password onChange={onChange} onBlur={onBlur} value={value} />
                )}
              />
            </FormControl>
          </Col>
        </Row>
        
        <div className="submit-container">
          <Button htmlType="submit" type="primary">
            Save Changes
          </Button>
        </div>
      </form>
    </Fragment>
  );
};

export default AgentCreateOrUpdateForm;
